.container-squares {
	height: 50%;
  z-index: -1;
	margin: 50px 0;
}

.dice {
	position: relative;
	margin: 0 auto;
	height: 50px;
	width: 50px;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-animation: spin 9s 3 alternate;
	        animation: spin 9s 3 alternate;
  z-index: -1;
}

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotateX(90deg) rotateY(360deg) translateZ(0px);
		        transform: rotateX(90deg) rotateY(360deg) translateZ(0px);
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotateX(90deg) rotateY(360deg) translateZ(0px);
		        transform: rotateX(90deg) rotateY(360deg) translateZ(0px);
	}
}

.side {
  margin: 0 auto;
	height: 25px;
	width: 25px;
	opacity: .8;
	transition: .5s;
  border-radius: 50%;
}

.dice:hover > .side {
	border-radius: 50%;
}

.one {
	-webkit-transform: rotateX(90deg) translateZ(100px);
	        transform: rotateX(90deg) translateZ(100px);
	background: #C20A59;
}

.two {
	-webkit-transform: translateZ(100px);
	        transform: translateZ(100px);
	background: #1F507A;
}

.three {
	-webkit-transform: rotateY(90deg) translateZ(150px);
	        transform: rotateY(90deg) translateZ(150px);
	background: #3383DF;
}

.four {
	-webkit-transform: rotateY(180deg) translateZ(100px);
	        transform: rotateY(180deg) translateZ(100px);
	background: #1F507A;
}

.five {
	-webkit-transform: rotateY(-90deg) translateZ(100px);
	        transform: rotateY(-90deg) translateZ(100px);
	background: #8D972A;
}

.six {
	-webkit-transform: rotateX(-90deg) translateZ(100px);
	        transform: rotateX(-90deg) translateZ(100px);
	background: #8C101D;
}
.cube-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-basis: 33%;
          flex-basis: 33%;
	overflow: hidden;
	z-index: 2;
  margin: 10px;

  border: 2px solid rgba(255,255,255,0.2);
	background: rgba(255,255,255,0.4);

	backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  box-shadow: 
    6px 6px 12px 0 rgba(0, 0, 0, 0.25),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  border-radius: 5px;
	transition: 500ms linear;
	will-change: transform;
}

.cube-item:hover {
  cursor: pointer;
  border: solid #d5d5d5;
}

.cube-item__image {
  margin: 0 auto;
  width: 150px;
  height: 150px;
}

.cube-item__description {
  /* border: solid 2px black; */
  /* border-top: solid black 2px; */
  text-align: center;
  background: rgba(0, 0, 0, 0.25);
}

.cube-item__description p {
  color: whitesmoke;
}

.cube-item__overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 30;
  opacity: 0.5;
}

@media screen and (min-width: 600px) {
  .cube-item {
    -webkit-flex-basis: 80% !important;
            flex-basis: 80% !important;
  }
  .cube-item__image {
    width: 200px;
    height: 200px;
  }
}
